#root {
  display: flex;
  justify-content: center;
}


/*
HEADER
*/

.Header {
  margin: 10px 0;
}

.Header-Logo {
  display: flex;

}

.Header-Logo span {
  padding: 0 16px;
}

.Header-Title {
  margin: 10px 0;
}

.Header-Link {
  background-color: white;
  color: black;
  font-size: 24px;
  text-decoration: none;
}

.Header-Link:hover {
  color: #61dafb;
}

.Header-navigation button {
  background-color: white;
  margin: 0.6em 1em;
  font-size: 16px;
  text-decoration: none;
  border: none;
}

.Header-navigation button:hover {
  border-bottom: 1px solid #a7a7a7;
}

/*
Gallery
*/

.Gallery {
  display: flex;
  flex-wrap: wrap;
}

.Panel {
  display: flex;
  justify-content: center;
}

.PanelOverlay .Carousel {
  position: initial;
}

.PanelOverlay {
  position: fixed;
  background-color: rgba(100, 100, 100, 0.4);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 900;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Carousel */

.Carousel {
  display: flex;
  justify-content: center;
  position: relative;
}

.Carousel-slide {
  display: flex;
  justify-content: center;
}

.Slide {
  display: flex;
  justify-content: center;
  object-fit: scale-down;
  width: inherit;
  height: inherit;
}

.Slide img {
  width: inherit;
  height: auto;
}

.Slide img, .Panel img {
  -webkit-box-shadow: 2px 3px 11px 1px rgba(0,0,0,0.46);
  -moz-box-shadow: 2px 3px 11px 1px rgba(0,0,0,0.46);
  box-shadow: 2px 3px 11px 1px rgba(0,0,0,0.46);
  border-radius: 4px;
}

.landscape img {
  width: 100%;
  height: auto;
}

.portrait img {
  width: auto;
  height: 100%;
}

.Home .Carousel-controls button svg path {
  fill: black;
}

.Carousel-controls button {
  text-decoration: none;
  border: none;
  font-size: 20px;
  margin: 0 1px;
  justify-self: center;
  align-self: center;
  background: none;
  width: 2.5em;
}

.PanelOverlay-container .Carousel-controls button svg path {
  fill: white;
}

.Carousel-controls svg path:hover {
  stroke: #797979;
  stroke-width: 1em;
}

.Carousel-controls {
  position: absolute;
  top: 48%;
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.Carousel-controls button:disabled {
  display: none;
}

.rotate180 {
  -webkit-transform:rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

.About {
  margin: 0.6em 1em;
  line-height: 1.5em;
  border-top: 1px black solid;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App {
    width: 100%;
  }

  .Header-Logo {
    flex-direction: column;
  }

  .Panel {
    width: 90%;
    padding: 0.25em;
    margin: 0.5em 0.75em;
  }

  .Home .Slide {
    width: 90%;
  }

  .Carousel-controls button {
    width: 2.5em;
  }
  .Carousel-slide {
    width: 90%;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .App {
    max-width: 600px;
  }

  .Panel {
    width: 234px;
    height: 159px;
    padding: 0.25em;
    margin: 0.5em 0.75em;
  }

  .Home .Slide {
    width: 480px;
    height: 320px;
  }
  .Carousel-slide {
    width: 500px
  }
}

/* Medium devices (landscape tablets, 820px and up) */
@media only screen and (min-width: 820px) {
  .App {
    max-width: 812px;
  }

  .Panel {
    width: 234px;
    height: 159px;
    padding: 0.25em;
    margin: 0.5em 0.75em;
  }

  .Home .Slide {
    width: 600px;
    height: 400px;
  }

  .Carousel-slide {
    width: 720px;
  }
}
/* Large devices (landscape tablets, 820px and up) */
@media only screen and (min-width: 1020px) {
  .App {
    max-width: 1000px;
  }

  .Panel {
    width: 234px;
    height: 159px;
    padding: 0.25em;
    margin: 0.5em 0.75em;
  }

  .Home .Slide {
    width: 840px;
    height: 560px;
  }

  .Carousel-slide {
    width: 900px
  }
}
